.vux-timeline {
  padding: 1rem;
}
.vux-timeline > ul > li {
  list-style: none;
}
.vux-timeline-item {
  position: relative;
}
.vux-timeline-item-content {
  padding: 0 0 1.5rem 1.2rem;
}
.vux-timeline-item-head,
.vux-timeline-item-head-first {
  position: absolute;
  content: '';
  z-index: 99;
  border-radius: 2.64rem;
}
.vux-timeline-item-head {
  width: 0.266667rem;
  height: 0.266667rem;
  left: 0.026667rem;
  top: 0.106667rem;
}
.vux-timeline-item-head-first {
  width: 0.533333rem;
  height: 0.533333rem;
  left: -0.106667rem;
  top: 0.133333rem;
}
.vux-timeline-item-tail {
  position: absolute;
  content: '';
  height: 100%;
  width: 0.053333rem;
  left: 0.133333rem;
  top: 0.133333rem;
  background-color: #04BE02;
}
.vux-timeline-item-checked {
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  width: 0.533333rem;
  height: 0.533333rem;
}
.vux-timeline-item-checked.weui-icon-success-no-circle::before {
  font-size: 0.373333rem;
  position: absolute;
  left: 0.08rem;
  top: 0.08rem;
  margin: 0!important;
  color: #FFF;
}
.vux-timeline-item-color {
  background-color: #04BE02;
}
.vux-fade-enter-active,
.vux-fade-leave-active {
  opacity: 1;
  -webkit-transition: opacity linear 0.2s;
  transition: opacity linear 0.2s;
}
.vux-fade-enter,
.vux-fade-leave-to {
  opacity: 0;
}
.vux-dialog-enter-active {
  -webkit-animation: vux-dialog-in 0.5s;
          animation: vux-dialog-in 0.5s;
}
.vux-dialog-leave-active {
  -webkit-animation: vux-dialog-out 0.3s;
          animation: vux-dialog-out 0.3s;
}
@-webkit-keyframes vux-dialog-in {
0% {
    -webkit-transform: scale(1.185);
            transform: scale(1.185);
    opacity: 0;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@keyframes vux-dialog-in {
0% {
    -webkit-transform: scale(1.185);
            transform: scale(1.185);
    opacity: 0;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@-webkit-keyframes vux-dialog-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
    opacity: 0;
}
}
@keyframes vux-dialog-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
    opacity: 0;
}
